<template>
	<view>
		<!-- 搜索 -->
		<view class="container search">
			<view class="flex">
				<view class="text">自定义查</view>
				<uni-datetime-picker class="date-picker" ref="formDate" v-model="start_time" type="date" placeholder="开始日期"/>
				至
				<uni-datetime-picker class="date-picker" ref="formDate" v-model="end_time" type="date" placeholder="结束日期"/>
				<button class="btn-search btn-color" @click="searchTime">查询</button>
			</view>
			<view class="flex mt-20">
				<picker class="picker" @change="changeWarehouse" :value="wIndex" :range="warehouse" :range-key="'warehouse_name'">
					<view v-if="wIndex == -1" class="uni-input">选择生产状态</view>
					<view v-else class="uni-input">{{warehouse[wIndex].warehouse_name}}</view>
					<image class="image" src="/static/image/picker.png"></image>
				</picker>
				<picker class="picker ml-20" @change="changeSale" :value="sIndex" :range="Sale_list" :range-key="'username'">
					<view v-if="sIndex == -1" class="uni-input">选择销售</view>
					<view v-else class="uni-input">{{Sale_list[sIndex].username}}</view>
					<image class="image" src="/static/image/picker.png"></image>
				</picker>
			</view>
		</view>
		<!-- 列表 -->
		<view class="container list">
			<view class="item" v-for="(item,index) in list" :key="item.id">
				<view class="big ellipsis">
					订单号{{item.order_number}}
				</view>
				<view class="flex top mb-10">
					<view class="title grey">客户名称</view>
					<view class="value">{{item.name}}</view>
				</view>
				<view class="flex top mb-10">
					<view class="title grey">销售员</view>
					<view class="value">{{item.username}}</view>
				</view>
				<view class="flex mb-10">
					<view class="title grey">交货日期</view>
					<view class="value">{{item.delivery_date}}</view>
				</view>
				<view class="flex mb-10 last">
					<view class="title grey">是否加急</view>
					<radio-group @change="changeCheckType($event,item,index)" class="check_box">
						<view v-for="(item_s,index_s) in is_urgent" :key="item_s.id" class="check">
							<radio class="checkClick round" :value="item_s.id" :checked="item.is_urgent ==item_s.id ">{{item_s.name}}</radio>
						</view>
					</radio-group>
				</view>
				<view class="arrow_text flex flex-column">
					<view style="background: #F0F0F0;color: #B9B9B9 ;" v-if="item.order_state == 1">待排尺</view>
					<view style="background: #FFF1EF;color: #FF3F25;" v-if="item.order_state == 3">待审核</view>
					<view style="background: #E8FAFF;color: #25D3FF;" v-if="item.order_state == 2">已排尺</view>
					<view style="background: #FFF1EF;color: #FF3F25;" v-if="item.order_state == 4">待生产</view>
					<view style="background: #FDF6EF;color: #FC9011;" v-if="item.order_state == 5">生产中</view>
					<view style="background: #EDF9F8;color: #34BCB5 ;" v-if="item.order_state == 6">已完成</view>
				</view>
				<view class="arrow arrow flex flex-column" @click="details(item.id)">
					<view>详情</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				wIndex: -1,
				warehouse: [
					{
						id:0,
						warehouse_name:"全部"
					},
					{
						id:3,
						warehouse_name:"待审核"
					},
					{
						id:4,
						warehouse_name:"待生产"
					},
					{
						id:5,
						warehouse_name:"生产中"
					},
					{
						id:6,
						warehouse_name:"已完成"
					},
				],
				list: [],
				start_time:'',
				end_time:'',
				order_state:'',
				page:0,
				is_urgent:[
					{
						id:1,
						name:"是",
					},
					{
						id:2,
						name:"否",
					},
				],
				sIndex:-1,
				Sale_list:[],
				create_user_id:'',
			}
		},
		onLoad() {
			this.page = 0
			 this.uid = this.common.get("uid")
			// 出库记录接口
			this.listInterface()
			// 销售
			this.http({
				url: this.api.getUserData,
				method: "POST",
				data: {
					uid: this.common.get("uid"),
					
				},
				success: res => {
					this.Sale_list = res
					let item = {
						id:'',
						username:"全部",
					}
					this.Sale_list.unshift(item)
				}
			})
		},
		//上拉触底函数
		onReachBottom(){
			// 列表接口
			this.http({
				url: this.api.Order,
				method: "POST",
				data: {
					uid: this.common.get("uid"),
					order_date_start:this.start_time,
					order_date_end:this.end_time,
					order_state:this.order_state,
					create_user_id:this.create_user_id,
					page:this.page,
					type:'yield',
				},
				success: res => {
					this.page+=1
					if(res.list.length == 0) {
						uni.showToast({
							title:'没有更多了',
							icon:'none',
							duration: 2000
						})
					} else {
						this.list = this.list.concat(res.list)
					}
				}, 
			})
		},
		methods: {
			changeCheckType(e,item,index) {
				this.list[index].is_urgent = e.detail.value
				this.http({
					url: this.api.up_is_urgent,
					method: "POST",
					data: {
						uid: this.common.get("uid"),
						id:item.id,
						is_urgent:e.detail.value
					},
					success: res => {
						
					}
				})
			},
			// 详情
			details(eve) {
				uni.navigateTo({
					url:'/pages/paichan/details/details?data='+encodeURIComponent(JSON.stringify(eve))
				})
			},
			// 时间查询
			searchTime() {
				this.page = 0
				this.listInterface()
			},
			// 库存列表点击
			changeWarehouse(e) {
				this.page = 0
				this.wIndex = e.target.value
				this.order_state = this.warehouse[this.wIndex].id
				this.listInterface()
			},
			// 销售点击
			changeSale(e) {
				this.page = 0
				this.sIndex = e.target.value
				this.create_user_id = this.Sale_list[this.sIndex].id
				this.listInterface()
			},
			// 列表接口
			listInterface() {
				this.http({
					url: this.api.Order,
					method: "POST",
					data: {
						uid: this.common.get("uid"),
						order_date_start:this.start_time,
						order_date_end:this.end_time,
						order_state:this.order_state,
						create_user_id:this.create_user_id,
						page:this.page,
						type:'yield',
					},
					success: res => {
						this.page+=1
						if(res.list.length == 0) {
							uni.showToast({
								title:'没有数据',
								icon:'none',
								duration: 2000
							})
							this.list = res.list
						} else {
							this.list = res.list
						}
					}
				})
			},
		}
	}
</script>

<style lang="scss">
.check_box {
	display: flex;
	justify-content: flex-start;
	flex-wrap: wrap;
	align-items: center;
	.check {
		width: 80rpx;
		margin-right: 7rpx;
		font-size: 24rpx;
		font-weight: 500;
		color: $color-3;
	}
}
.btn-color {
	width: 120rpx!important;
	height: 64rpx;
	line-height: 64rpx;
	background: $color-b5;
}
.search .uni-date-editor--x .uni-date__icon-clear {
	top: -4rpx;
}
.uni-date-single {
	padding: 0!important;
	height: 64rpx;
}
.date-picker {
	width: 42%!important;
}
.uniui-calendar {
	display: none;
}
.arrow_text {
	position: absolute;
	right: 0;
	top: 30rpx;
	view{
		font-size: 20rpx;
		font-weight: 400;
		padding: 12rpx;
		border-radius: 32rpx 0px 0px 32rpx;
	}
}
.arrow_jt {
	position: absolute;
	right: 30rpx;
	bottom: 30rpx;
	image {
		width: 29.96rpx;
		height: 16rpx;
	}
}
</style>
